/*------------------------------------*\
  #CONTAINER
\*------------------------------------*/
.container {
  width: 100%;
  margin-inline: auto;
  max-width: 1300px;
}
@media (max-width: 1280px) {
  .container {
    max-width: 74.5rem;
  }
}
@media (max-width: 1024px) {
  .container {
    max-width: 58.5rem;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 38rem;
  }
}
@media (max-width: 640px) {
  .container {
    max-width: 100%;
  }
}
@media (max-width: 414px) {
  .container {
    max-width: 23.875rem;
  }
}
@media (max-width: 376px) {
  .container {
    max-width: 19.5rem;
  }
}
@media (max-width: 360px) {
  .container {
    max-width: 17rem;
  }
}

/*------------------------------------*\
  #GRID COLUMNS
\*------------------------------------*/
.row {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
}

