/*------------------------------------*\
  #BASE RESET
\*------------------------------------*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  line-height: normal;
  font-family: "Geologica", sans-serif;
  font-size: 16px;
}

/*------------------------------------*\
  #MEDIA ELEMENTS
\*------------------------------------*/
img,
video,
svg {
  max-width: 100%;
  height: auto;
}

/*------------------------------------*\
  #HEADINGS & TEXT
\*------------------------------------*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

/*------------------------------------*\
  #LISTS
\*------------------------------------*/
ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

/*------------------------------------*\
  #LINKS
\*------------------------------------*/
a {
  text-decoration: none;
  color: inherit;
}

/*------------------------------------*\
  #BUTTONS
\*------------------------------------*/
button {
  background: none;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

/*------------------------------------*\
  #FORMS
\*------------------------------------*/
input,
textarea,
select {
  font: inherit;
  margin: 0;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

textarea {
  resize: vertical;
}

/*------------------------------------*\
    #TABLES
  \*------------------------------------*/
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*------------------------------------*\
    #MISC
  \*------------------------------------*/
fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

hr {
  border: none;
  border-top: 1px solid #e5e5e5;
  margin: 24px 0;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: normal;
  padding: 17px 22px;
  font-family: "Geologica", sans-serif;
  font-size: var(--body-3);
  border-radius: 0.5rem;
  border: 1px solid;
  line-height: 100%;
  font-weight: 700;
}
.button.button--solid {
  color: #242424;
  background-color: #F0E8DD;
  border-color: #F0E8DD;
}
.button.button--outlined {
  color: #F0E8DD;
  background-color: transparent;
  border-color: #F0E8DD;
}

.header {
  padding: 15px 0;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #F0E8DD;
}
.header__nav ul {
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: center;
}
.header__actions {
  display: flex;
  align-items: center;
  gap: 20px;
}
.header.header--absolute {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 9;
}

.hero {
  background-image: url("../img/hero.jpg");
  background-size: cover;
  background-position: center center;
  height: 800px;
  padding-top: 100px;
  position: relative;
}
.hero::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.hero__info {
  position: relative;
  z-index: 2;
  padding: 30px;
  max-width: 750px;
  color: #F0E8DD;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border-radius: 0.5rem;
}
.hero__info-advantages {
  margin-top: 30px;
  display: flex;
  align-items: center;
  gap: 35px;
}
.hero__info-adv {
  display: flex;
  gap: 10px;
  align-items: center;
}
.hero__info-btns {
  display: flex;
  gap: 20px;
  margin-top: 30px;
}
.hero > .container {
  height: 100%;
}
.hero__wrppaer {
  height: 100%;
  align-items: center;
  display: flex;
}