/*------------------------------------*\
  #BASE RESET
\*------------------------------------*/
*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    font-size: 100%;
    -webkit-text-size-adjust: 100%;
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    line-height: normal;
    font-family: 'Geologica', sans-serif;
    font-size: 16px;
}

/*------------------------------------*\
  #MEDIA ELEMENTS
\*------------------------------------*/

img,
video,
svg {
    max-width: 100%;
    height: auto;
}

/*------------------------------------*\
  #HEADINGS & TEXT
\*------------------------------------*/

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

/*------------------------------------*\
  #LISTS
\*------------------------------------*/

ul,
ol {
    padding: 0;
    margin: 0;
    list-style: none;
}

/*------------------------------------*\
  #LINKS
\*------------------------------------*/

a {
    text-decoration: none;
    color: inherit;
}


/*------------------------------------*\
  #BUTTONS
\*------------------------------------*/

button {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
}

button:disabled {
    cursor: not-allowed;
    opacity: 0.6;
}

/*------------------------------------*\
  #FORMS
\*------------------------------------*/

input,
textarea,
select {
    font: inherit;
    margin: 0;
}

input:focus,
textarea:focus,
select:focus {
    outline: none;
}

textarea {
    resize: vertical;
}

/*------------------------------------*\
    #TABLES
  \*------------------------------------*/

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*------------------------------------*\
    #MISC
  \*------------------------------------*/

fieldset {
    border: none;
    margin: 0;
    padding: 0;
}

hr {
    border: none;
    border-top: 1px solid #e5e5e5;
    margin: 24px 0;
}